import coinJson from '../../static/json/coin.json'
export default {
	data() {
		return {
			coins: [],
			coinName: '',
			coinIndex: -1,
			easteregg: false,
			showTop: false
		}
	},
	onLoad() {
		this.getCoinList()
		this.getUseCoin()
		this.getEasteregg()
	},
	methods: {
		// 获取硬币列表
		getCoinList() {
			let list = coinJson.data
			this.coins = []
			for (let item of list) {
				let data = {
					name: item.name,
					id: item.src,
					src: `https://tosscoin-1256354221.cos.ap-shanghai.myqcloud.com/img/${item.src}_back.png`
				}
				this.coins.push(data)
			}
		},
		// 获取当前使用硬币
		getUseCoin() {
			this.coinName = uni.getStorageSync('coinName') || '2020shu'
		},
		// 获取彩蛋
		getEasteregg() {
			this.easteregg = uni.getStorageSync('easteregg') || false;
		},
		// 选中硬币
		selectCoin(id, index) {
			this.coinIndex = index
			this.coinid = id
		},
		// 设置硬币皮肤
		setCoin() {
			uni.setStorageSync('coinName', this.coinid);
			this.getUseCoin();
		},
		// 输入彩蛋码
		checkCode(e) {
			let code = e.detail.value
			if (code === 'cd52078x8') {
				uni.showToast({
					title: '恭喜获得520彩蛋硬币',
					icon: 'none',
					duration: 2000
				});
				uni.setStorageSync('easteregg', true);
				this.getEasteregg()
			} else {
				uni.showToast({
					title: '彩蛋码错误',
					icon: 'none',
					duration: 2000
				});
			}
			this.showTop = false;
		},
		// 停止下拉刷新
		onPullDownRefresh() {
			this.showTime =  setTimeout(() => {
				uni.stopPullDownRefresh();
				this.showTop = true;
			}, 500);
		},
		// 清除定时器
		clearShowTime() {
			clearTimeout(this.showTime);
			this.showTime = null;
			console.log('showTime', this.showTime)
		},
	},
	onHide: function() {
		this.showTime && this.clearShowTime();
	}
}
